.loader {
  margin: 50px auto 0px auto;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 3px dashed darkblue;
  animation: rotate 1.5s infinite linear;
}

@keyframes rotate {
  from {
    transform: rotate(0deg) scale(1);
  }
  to {
    transform: rotate(360deg) scale(1.2);
  }
}
